export default {"name":"霍山黄大茶","img":"\\huangcha\\huoshanhuangdacha\\img_2019112210180313ydxp.jpg","desc":["霍山黄大茶，属黄茶，亦称为皖西黄大茶，自明朝便已有记载。产于安徽霍山、金寨、大安、岳西等地。叶大、梗长、黄色黄汤香高耐泡，饮之有消垢腻、去积滞之作用，具有抗辐射，提神清心，消暑等功效。其成品主要经过炒茶、初烘、堆积、烘焙等工序，为国家地理标志保护农产品。"],"menus":[{"name":"霍山黄大茶茶叶历史","content":[{"text":"明代许次纾《茶疏》记述：“天下名山，必产灵草。大江以北，霍山县茶生最多，名品也振于南(方);山陕人皆用之。南方谓其能消垢腻，去积滞，亦甚宝爱。顾彼山中不善制造，就于食铛大薪炒焙，未及出釜，业已焦枯，讵堪用哉。兼以竹造区筒乘热便贮，虽有缘枝紫笋，辄就萎黄，仅供下食，奚堪品斗。”这篇着作记载了黄大茶的原产地，销售市场，生产器具，贮存包装，药用功能等作了系统的论述，尤其是这被记载的工艺与现时黄大茶的制法，大致吻合。焦味和闷黄，正是黄大茶的本质特征。"},{"text":"清代乾隆四十一年(1776)的《霍山县志》里记载：“茶，本山货属，以茶为冠。其品之最上者日银针，次日雀舌，又次日梅花片，白兰花头日松萝。”"},{"text":"中国茶叶泰斗陈椽在《安徽茶经》记述：\n        黄茶\n        产地较主阔，浙江的平阳、东阳、霍山、岳西、金寨、六安及湖北英山出产黄大茶，台湾也有黄茶生产，其中以霍山县黄大茶为最著名，产量最多。"},{"src":"\\huangcha\\huoshanhuangdacha\\img_20191122101803inyfnq.jpg"}]},{"name":"霍山黄大茶茶叶特点","content":[{"text":"叶大、梗长、黄色黄汤香高耐泡，饮之有消垢腻、去积滞之作用。又具有浓裂的老火香(俗称锅耙香)。著名的品种有安徽的霍山黄大茶、广东的大叶青等。霍山黄大茶外形梗壮叶肥，叶片成条，梗叶吉相连形似钓鱼钩，梗叶金黄显褐，色泽油润，汤色深黄显褐，叶底黄中显褐，滋味浓厚醇和，具有高嫩的焦香。"},{"src":"\\huangcha\\huoshanhuangdacha\\img_20191122101803sa2yxu.jpg"}]},{"name":"霍山黄大茶加工工艺","content":[{"text":"霍山黄大茶加工技术加工工序为炒茶、初烘、堆积、烘焙。"},{"text":"炒茶"},{"text":"炒茶分生锅、二青锅、熟锅三锅连续操作。生锅主要起杀青作用，破坏酶的活性;二青锅主要起初步揉条和继续杀青的作用;熟锅主要是进一步做条。炒茶锅都采用普通饭锅，按倾斜25。～30。砌成相连的三口锅炒茶灶。炒茶都使用竹丝扎成的炒茶扫把，长约1米，竹丝一端分散直径约为10厘米。所不同的是炒法，当地茶农总结为“生锅要旋，二锅带劲，熟锅要钻”，另外锅温也有所不同。生锅锅温为180℃～200℃，投叶250～500克，两手支起炒茶扫把在锅中旋转炒拌，使叶子随着扫把旋转翻动，受热均匀，此即“满锅旋”。同时注意旋转要快，用力要匀，并不断翻转抖扬，散发水蒸气。炒3～5分钟，叶质柔软、叶色暗绿时，可扫入二青锅内继续炒制。二青锅锅温略低于生锅。进入二青锅后，及时用炒茶扫把将叶子困住在锅中旋转，转圈要大，用力也较生锅大，即“带把劲”，使叶子顺着炒把转，而不能赶着叶子转，否则满锅飞，起不到揉捻作用。然后再加上炒揉，用力逐渐加大，做紧条形，通过多次炒揉，当叶片皱缩成条、茶叶黏着叶面、有黏手感时，可扫人熟锅继续做形。熟锅温度更低，为130℃～150℃，炒茶方法基本同二青锅。所不同的是增加了旋转搓揉，使叶子吞吐于竹丝扫把问，即“钻把子”。如此炒揉、搓揉连续不断进行，待炒到条索紧细、发出茶香、达三四成干时，便可出锅，进行初烘。"},{"text":"初烘"},{"text":"炒后立即高温快速烘焙，温度为120℃左右，投叶量为每烘笼2～2.5千克，每2～3分钟翻烘一次，烘约30分钟，达七八成干，茶梗折之能断，即为适度。这样就可下烘堆积或者直接交售给茶站，由茶站统一堆积。"},{"text":"堆积"},{"text":"下烘后将茶叶趁热装篓或堆积于圈席内，稍加压紧，高约1米，放置在高燥的烘房内，利用烘房的热促进蒸变。堆积时间长短可视鲜叶老嫩、茶坯含水量大小及其黄变程度而定，一般要求5～7天。茶站对收来的茶叶，先进行拉小火，烘到九成干，而后堆积，堆积时间相对长一点。堆积到叶色黄变，香气透露，即为适度，可开堆进行烘焙。"},{"text":"烘焙"},{"text":"利用高温进一步促进黄变和内质的转化，以形成黄大茶特有的焦香味。烘焙是采用栎炭明火，温度为130。C～150。C，每烘笼投叶约12.5千克，两人抬笼，仅几秒钟就翻动一次，翻叶要轻快而匀，防止断碎和茶末落入火中产生烟味。火功要高，烘得足，这样色香味才能得到充分提升，时间为40～60分钟，待茶梗折之即断，梗心呈菊状，茶梗显露金色光泽，芽叶上霜，焦香明显，即可下烘，趁热踩篓包装。"},{"src":"\\huangcha\\huoshanhuangdacha\\img_20191122101803pgmeaf.jpg"}]},{"name":"霍山黄大茶储藏方法","content":[{"text":"黄茶\n        是一种介于\n        绿茶\n        和\n        乌龙茶\n        之间的茶种，保存黄茶，其基本方法与绿茶和乌龙茶类似。一般来说，黄茶应存放在密闭、阴凉、干燥、无异味、避光的环境下。保存得当的话，黄茶可长期存放。"},{"src":"\\huangcha\\huoshanhuangdacha\\img_201911221018037o3cz0.jpg"}]},{"name":"霍山黄大茶冲泡方法","content":[{"text":"备具：准备好\n        茶具\n        及霍山黄大茶。"},{"text":"温壶涤具：茶具中置入烧开的清水，主要起到温壶温杯的作用同时可以涤具。"},{"text":"投茶：将霍山黄大茶小心置入壶中。"},{"text":"润茶：沸水置入壶中，快速倒去以唤醒茶叶。"},{"text":"冲茶浸润：根据实际情况掌握冲泡时间。"},{"text":"分茶：壶中的茶叶现过滤于公道杯中，同时保持茶汤均匀，再分别均匀的分入小杯中。"}]},{"name":"霍山黄大茶功效作用","content":[{"text":"主要功效"},{"text":"抗辐射，提神清心，消暑。"},{"text":"保健功能"},{"text":"黄茶\n        是轻度发酵茶，各种品质特征偏向于\n        绿茶\n        ，的香气和醇厚口感，适宜绝大多数人品饮享用。同时又改善了绿茶的苦涩味道，增加了发酵茶黄茶富含茶多酚、氨基酸、维生素、多糖、矿物质及纤维素等有益成分，在调理消化系统，保护心脑血管，抗氧化、抗衰老，防癌抗癌等方面具有积极功效。"},{"text":"同时，黄茶还有提神醒脑、敌烟醒酒、排毒解毒和消炎杀菌等功效。黄茶茶性寒凉，鲜爽适口，香气怡人，尤其适宜在夏天饮用。"}]},{"name":"霍山黄大茶历史故事","content":[{"text":"皖西为古代寿州、舒州辖境，唐宋以来盛产茶叶，这里所产的霍山黄芽、天柱香芽等在唐朝就盛名远扬，为文人墨 客广为传颂，留下文字记载颇多。而明朝以后，随着炒青制 法的出现，皖西一带先后创制出大兰\n        花茶\n        、小兰花茶、绿大 茶、绿小茶、黄大茶、黄小茶。大茶一般为一芽三、四叶原 料所制，甚至有五、六叶者，叶大梗长，炒焙方法大同小异;小茶为一芽一、二叶所制，又称芽茶，多为贡品。关于这些 \"大茶\"的制法和品质特点，古籍中难以寻踪，而流传于民间 的乡土口头文学和神话倒是不少。但是随着朝代变迁，也多 自生自灭。著作权归本站所有。商业转载请联系本站获得授权，非商业转载请注明出处。"}]},{"name":"霍山黄大茶获奖情况","content":[{"text":"2010年霍山黄大茶被国家农业部认定为国家地理标志保护农产品。"}]},{"name":"霍山黄大茶鉴别好茶","content":[{"text":"霍山黄大茶茶叶的好坏，主要从色、香、味、形四个方面鉴别。"},{"text":"看外形"},{"text":"霍山黄大茶是以大枝大叶的外形为特点，因大枝大叶的茶比较罕见，所以极易辨识。通常以此特征来鉴别霍山黄大茶的真伪。外形梗壮叶肥，叶片成条，梗部似鱼钩;色泽鲜亮、金黄油润;叶底绿黄，叶质柔软厚实。"},{"text":"看色泽"},{"text":"霍山黄大茶干茶外形紧索，呈黄褐色。\n        黄茶\n        的品质特色是\"黄叶黄汤\"。霍山黄大茶梗叶金黄显褐，色泽油润，汤色深黄显褐，叶底黄中显褐。"},{"text":"嗅香气"},{"text":"茶香被誉为“天下第一香”历来为茶人看重。霍山黄大茶滋味浓厚醇和，具有高嫩的焦香，黄大茶产品按品质优次分3级6等。"},{"text":"品茶味"},{"text":"通常一杯好茶必须具有丰富的滋味，它不但要求茶汤的味道有不断滋生的感觉;而且咽下后，任然产生无穷的回味和余韵，霍山黄大茶滋味浓厚醇和。"}]}]}